<template>
  <div class="component py-4 details">
    <h5 class="component__heading px-3 px-lg-5">Bet Details</h5>
    <div v-if="bet">
      <div class="table-responsive">
        <Table>
          <thead>
            <tr>
              <th class="pl-3 pl-lg-5">Date</th>
              <th>BetId</th>
              <th>Account</th>
              <th>Amount</th>
              <th>Possible Win</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td class="date pl-3 pl-lg-5">
                <span class="date__time">{{ bet.time_placed |formatUTCLocalTime }}</span>
                <br>
                <span class="date__day">{{ bet.time_placed |betDate }}</span>
              </td>
              <td>{{ bet.bet_id }}</td>
              <td>{{ bet.bet_account }}</td>
              <td>Ksh {{ bet.amount_staked }}</td>
              <td>Ksh {{ bet.possible_win }}</td>
            </tr>
          </tbody>
        </Table>
      </div>
      <div class="row p-4">
        <div class="col">
          <h6 class="component__subheading px-3 px-lg-5 mt-3">Games</h6>
        </div>
        <!--<div class="col">-->
          <!--<BetButton class="float-right" @click="copyBet(bet)" :disabled="betCanBetCopied(bet)">-->
            <!--<i class="fa fa-copy"></i>-->
            <!--<span>Copy</span>-->
          <!--</BetButton>-->
        <!--</div>-->
      </div>

      <div class="table-responsive">
        <Table>
          <thead>
            <tr>
              <th class="pl-3 pl-lg-5">Match</th>
              <th>Market</th>
              <th>Picked</th>
              <th>Odd</th>
              <th>Ht</th>
              <th>Ft </th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(pick, pickIndex) in bet.picks.data" :key="pickIndex">
              <td class="pl-3 pl-lg-5">{{ pick.teams }}</td>
              <td>{{ pick.market_name }} {{parseInt(pick.handicap) > 0 ? pick.handicap : ''}}</td>
              <td>{{ pick.picked }} {{parseInt(pick.handicap) > 0 ? pick.handicap : ''}} </td>
              <td>{{ pick.odd_value }}</td>
              <td>{{ pick.ht }}</td>
              <td>{{ pick.ft }}</td>
              <td>{{ pick.status }}</td>
              <!--<td :class="pick.status ==='won' ? 'green' : 'red'"><i class="fa "-->
                  <!--:class="pick.status ==='won' ? 'fa-check green' : 'fa-times red'"></i>-->
              <!--</td>-->
            </tr>
          </tbody>
        </Table>
      </div>
    </div>
    <div class="py-1 py-lg-3 px-3 px-lg-5">
      <BetButton @click="$router.push({name: 'bet_history'})">
        <i class="fa fa-arrow-left"></i>
        <span>Back</span>
      </BetButton>
      <BetButton class="float-right"
                 @click="cancelBet(bet, bet.id)"
                 v-if="!$gameHelper.isStarted(bet) && betCanBetCancelled(bet) && bet.status !== 'cancelled'"
      >
        <i class="fa fa-times"></i>
        <span>Cancel bet</span>
      </BetButton>
    </div>
  </div>
</template>

<script>
  import Table from '../Utilities/Table'
  import BetButton from '../Utilities/BetButton'
  import {AUTH_CONFIG} from "../../auth_config";
  import {CANCEL_BET_URL} from '../../api_routes';
  import axios from 'axios'
  import moment from 'moment'
  import {GAME} from '../../games'

  export default {
    name: 'BetDetail',
    components: {Table, BetButton},
    data () {
      return {
        bet: null
      }
    },

    mounted () {
      this.loadBet()
    },

    methods: {
      loadBet () {
        let bet = JSON.parse(window.localStorage.getItem('betDetail'))
        if (bet) {
          this.bet = bet
        }
      },
      betCanBetCancelled (bet) {
          if (!bet) return
          let currentTime = moment().subtract(10, "minutes").format('YYYY-MM-DD HH:mm:ss')
          let timeBetPlaced = moment.utc(bet.time_placed).local().format('YYYY-MM-DD HH:mm:ss')
          if (timeBetPlaced > currentTime) return true
          else return false
      },
      betCanBetCopied (bet) {
          if (!bet) return
          let currentTime = moment().subtract(5, "minutes").format('YYYY-MM-DD HH:mm:ss')
          let timeBetPlaced = moment.utc(bet.time_placed).local().format('YYYY-MM-DD HH:mm:ss')
          if (timeBetPlaced < currentTime) return true
          else return false
      },

      cancelBet (bet, betId) {
          if (!this.betCanBetCancelled(bet) || this.$gameHelper.isStarted(bet)) {
              this.$noty.error('Time for canceling this bet is expired')
              return
          }
          let postData = {
              bet_id : betId
          }
        axios.post(CANCEL_BET_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then((resp) => {
            if (resp.status === 200) {
                this.$noty.success('The bet was successfully cancelled')
                this.$store.dispatch('init_auth_user', {request: 'refresh'})
                this.$router.go(-1)
            }
            }, () => {
            this.$noty.error('There was a problem canceling your bet.')
            })
      },

      copyBet (bet) {
        if (!bet) return
        bet.picks.data.forEach(pick => {
            if (!GAME.isStarted(pick)) {
                this.$store.dispatch('updateBetSlip', pick)
            }
        })
      }

    }
  }
</script>

<style scoped lang="stylus">
  .red
    color red !important
  .green
    color green !important
</style>
